@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
	--font-family: 'Inter', sans-serif;

	--gradient-text: linear-gradient(270.11deg, #8155BA 0.08%, #BEAFC2 94.04%);
	--gradient-bg: linear-gradient(89.83deg, #ffffff 1.11%, #f3faff 99.88%);

	--color-heading: #695E93;
	--color-navlinks: #5c5c5c;
	--color-text: #878899;
	--color-input-border: #00000029;
	--color-input-placeholder: #00000033;

	--color-warning-bg: rgba(255, 205, 68, 0.651);
	--color-terminate-bg: rgba(255, 68, 68, 0.651);
}

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.landing-navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0.8rem 3rem;
	font-family: var(--font-family);
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 12;
	background-color: var(--gradient-bg);
	-webkit-backdrop-filter: blur(12px);
	        backdrop-filter: blur(12px);
	border-bottom: 1px solid var(--color-input-border);
}

.landing-navbar .landing-navbar-links {
	grid-gap: 5rem;
	gap: 5rem;
	display: flex;
	font-weight: 500;
	font-size: 1.1rem;
	margin: 0 4rem;
}

.landing-navbar .landing-navbar-links p {
	color: var(--color-navlinks);
	transition: 0.2s all ease;
}

.landing-navbar .landing-navbar-links p:hover {
	color: #281C2D;
}

.landing-navbar-logo img {
	width: 65%;
}

.ctabutton {
	background-color: #281C2D;
	color: #fff;
	font-family: Inter;
	font-size: 1rem;
	font-weight: 600;
	border: none;
	padding: 1.1rem 2.75rem;
	border-radius: 0.9rem;
}

input {
	width: 100%;
	border: 1px solid var(--color-input-border);
	padding: 1rem 2rem;
	border-radius: 0.9rem;
	font-family: Inter;
	font-size: 1rem;
	font-weight: 400;
}

input:active,
input:focus {
	outline: none;
}

input:placeholder {
	color: var(--color-input-placeholder);
}



.hide {
	display: none;
}

.copy-link {
	display: flex;
	align-items: center;
	grid-gap: 2rem;
	gap: 2rem;
}

.copy-link .copy-link-input {
	min-width: 20rem;
	max-width: 30rem;
}

.copy-link .copy-link-input input {
	padding: 0.8rem 1rem;
}

.terminated {
	border: 1px solid #00000016;
	padding: 2rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	grid-gap: 2rem;
	gap: 2rem;
	max-width: 25rem;
	min-width: 24rem;
	border-radius: 1rem;
	min-height: 12rem;
}

.terminated .terminated-details {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	grid-gap: 0.3rem;
	gap: 0.3rem;
}

.terminated .terminated-details h4 {
	color: #000000B2;
	font-style: normal;
	font-weight: 500;
}

.terminated .btns {
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.terminated .btns button {
	background-color: var(--gradient-bg);
	font-family: Inter;
	font-size: 1rem;
	font-weight: 600;
	border: none;
	padding: 0.8rem 2rem;
	border-radius: 0.9rem;
	box-shadow: 4px 5px 30px 2px rgba(0, 28, 80, 0.045);
	transition: 0.3s all ease;
}

.terminated .btns button:hover,
.terminated .btns button:focus {
	outline: none;
	box-shadow: 4px 5px 30px 2px rgba(0, 28, 80, 0.1);
}

.terminated .btns .terminate-btn {
	color: #ff5b5b;
}

.terminated .btns .continue-btn {
	color: #009320;
}

.client-create .logo img {
	width: 8rem;
}

.client-create {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 2em;
	gap: 2em;
	margin: 1.5rem;
}

.client-create .create-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 2em;
	gap: 2em;
}

.input-fields {
	max-width: 40vw;
	min-width: 30vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 1.2em;
	gap: 1.2em;
}

.input-fields input {
	padding: 0.9rem 1.8rem;
	font-size: 1rem;
}

.admin-dashboard .logo img {
	width: 8rem;
}

.admin-dashboard {
	display: flex;
	flex-direction: column;
	align-items: center;
	grid-gap: 2em;
	gap: 2em;
	margin: 1.5rem;
}

.admin-dashboard .test-dashboard {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;
	grid-gap: 2em;
	gap: 2em;
	padding: 2rem;
}

.test-items {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 2rem;
	gap: 2rem;
}

.test-items .test-item {
	display: flex;
	align-items: center;
	justify-content: center;
	grid-gap: 4rem;
	gap: 4rem;
}

.copy-link button {
	color: var(--color-heading);
	background-color: var(--gradient-bg);
	font-weight: 600;
	font-size: 1rem;
	border: none;
	padding: 1rem 2rem;
	border-radius: 0.9rem;
	max-width: 8rem;
}

.landing-page {
	font-family: var(--font-family);
	padding: 6rem 1rem;
}

.landing-content {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	max-width: 45rem;
	grid-gap: 2rem;
	gap: 2rem;
	margin: 5rem 0 0;
}

.landing-content .headings {
	display: flex;
	flex-direction: column;
}

.landing-content .headings .sub-text {
	font-weight: 600;
	font-size: 3.6rem;
	letter-spacing: -2px;
	color: var(--color-heading);
}

.landing-content .headings .main-heading {
	font-weight: 700;
	font-size: 3.8rem;
	letter-spacing: -2px;
}

.landing-content .desc {
	color: var(--color-text);
	font-size: 1.4rem;
}

.landing-cta {
	margin: 4rem 0 8rem;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	grid-gap: 3rem;
	gap: 3rem;
}

.landing-cta .input-item {
	max-width: 35rem;
	min-width: 30rem;
	display: flex;
	align-items: center;
	grid-gap: 2rem;
	gap: 2rem;
}

.landing-cta .desc {
	color: var(--color-text);
	font-weight: 600;
	font-size: 1rem;
}

/* LANDING SECTION - 2 */

.features-content {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	padding: 4rem 2rem;
	height: 100vh;
}

.features-content .curr-heading {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 1em;
	gap: 1em;
}

.features-content p {
	font-size: 2rem;
}

.features-content h2 {
	font-size: 3rem;
}

.features-content .all-features {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 4rem 8rem;
	gap: 4rem 8rem;
}

.features-content .all-features .single-feature {
	font-size: 1.25rem;
	text-align: center;
}

.mid-cta .phew {
	font-size: 1rem;
	text-align: center;
	color: var(--color-text);
}

.features-content .mid-cta,
.final-features .mid-cta {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	grid-gap: 3rem;
	gap: 3rem;
}

/* LANDING SECTION - 3 */

.final-features {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 6rem 0;
}

.final-features .top-sec {
	display: grid;
	align-items: center;
	justify-content: center;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 4rem;
	gap: 4rem;
	margin: 2rem;
}

.final-features .top-sec .left-text,
.final-features .top-sec .right-text {
	display: flex;
	flex-direction: column;
	grid-gap: 2rem;
	gap: 2rem;
}

.final-features .top-sec .right-text {
	text-align: right;
}

.final-features .top-sec .left-text h3,
.final-features .top-sec .right-text h3 {
	font-weight: 500;
	font-size: 1.7rem;
}

.final-features .top-sec .left-text h1,
.final-features .top-sec .right-text h1 {
	font-weight: 600;
	font-size: 2.5rem;
}

.final-features .top-sec .infinite {
	display: flex;
	justify-content: center;
	align-items: center;
}

.final-features .top-sec .infinite img {
	width: 12rem;
}

.final-features .final-cta {
	margin: 5rem;
}

.user-login .logo img {
	width: 8rem;
}

.user-login {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 2em;
	gap: 2em;
	margin: 1.5rem;
}

.user-login .login-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 3em;
	gap: 3em;
}

.login-form .input-fields {
	max-width: 40vw;
	min-width: 30vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 1.75em;
	gap: 1.75em;
}

.input-fields input {
	padding: 0.9rem 1.8rem;
	font-size: 1rem;
}

.user-register .logo img {
	width: 8rem;
}

.user-register {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 2em;
	gap: 2em;
	margin: 1.5rem;
}

.user-register .register-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 2em;
	gap: 2em;
}

.register-form .input-fields {
	max-width: 40vw;
	min-width: 30vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 1.2em;
	gap: 1.2em;
}

.input-fields input {
	padding: 0.9rem 1.8rem;
	font-size: 1rem;
}

.image-capture {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.image-capture button {
	color: #5880f0;
	background-color: #fff;
	font-family: Inter;
	font-size: 1rem;
	font-weight: 600;
	border: none;
	padding: 1rem 2.75rem;
	border-radius: 0.9rem;
	box-shadow: 10px 9px 40px 7px rgba(177, 202, 255, 0.21);
	-webkit-box-shadow: 10px 9px 40px 7px rgba(177, 202, 255, 0.21);
	-moz-box-shadow: 10px 9px 40px 7px rgba(177, 202, 255, 0.21);
}

.image-capture .captured {
	margin: 4rem 0;
}

.image-capture .proceed {
	margin: 5rem 0 0;
}

.image-capture img {
	z-index: 10;
	height: 200px;
	position: absolute;
}

.image-capture .captured {
	z-index: 10;
	height: 250px;
	position: absolute;
}

.image-capture img {
	z-index: 10;
	height: 200px;
	position: absolute;
}

.status-dashboard .logo img {
	width: 8rem;
}

.status-dashboard {
	display: flex;
	flex-direction: column;
	align-items: center;
	grid-gap: 1.5em;
	gap: 1.5em;
	margin: 1.5rem 1.5rem 6rem;
}

.status-dashboard .charts {
	width: 40vw;
}

.status-dashboard .charts .two-charts {
	display: flex;
	align-items: center;
	justify-content: center;
}

.status-dashboard .test-details {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;
	grid-gap: 2em;
	gap: 2em;
	padding: 2rem;
}

.test-details {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.test-details .test-item {
	display: flex;
	align-items: center;
	justify-content: center;
	grid-gap: 4rem;
	gap: 4rem;
}

.copy-link button {
	color: var(--color-heading);
	background-color: var(--gradient-bg);
	font-weight: 600;
	font-size: 1rem;
	border: none;
	padding: 1rem 2rem;
	border-radius: 0.9rem;
	max-width: 8rem;
}

.status-dashboard .terminated-students {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 80vw;
	grid-gap: 3rem;
	gap: 3rem;
}

.status-dashboard .terminated-students .terminated-boxes {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 2rem;
	gap: 2rem;
}

.exam-container {
	display: grid;
	grid-template-columns: 1fr 4fr 1fr;
}

.exam-container .exam-details {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	grid-gap: 1.2rem;
	gap: 1.2rem;
	margin: 6rem 2rem;
	padding: 1rem 2rem;
	border: 1px solid var(--color-input-border);
	border-radius: 1rem;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	max-width: 15rem;
}

.exam-container .exam-details .details {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	grid-gap: 1rem;
	gap: 1rem;
	color: var(--color-navlinks);
}

.exam-container .exam-details .details h4 {
	font-style: normal;
	font-weight: 400;
	word-wrap: wrap;
}

.form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100vh;
	background-color: var(--gradient-bg);
	grid-gap: 1em;
	gap: 1em;
	border: none;
}

.embedded-form .form h2 {
	display: flex;
	text-align: center;
	align-self: center;
}

.form iframe {
	border: none;
	width: 50vw;
	height: 90vh;
}

.timer {
	margin: 6rem 2rem;
	padding: 1rem 2rem;
	border: 1px solid var(--color-input-border);
	border-radius: 1rem;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	max-width: 9rem;
	display: flex;
	align-items: center;
	justify-content: center;
}

.responsive-message {
	display: none;
}

.disable {
	margin-left: 3rem;
	background-color: var(--color-warning-bg);
	z-index: 10;
	position: absolute;
	width: 60vw;
	height: 100vh;

	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.terminate {
	background-color: var(--color-terminate-bg);
}

.blur {
	filter: blur(8px);
	-webkit-filter: blur(8px);
}

.hide {
	display: none;
}

@media only screen and (max-width: 800px) {
	h2 {
		font-size: 1.25rem;
		font-weight: 500;
	}

	.responsive-message {
		display: block;
	}

	.form-link,
	.form h2,
	.exam-details,
	.timer,
	.image-capture {
		display: none;
		visibility: hidden;
	}
}

.left-column .image-capture {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-top: 5rem;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	scroll-behavior: smooth;
}

body {
	background: var(--gradient-bg);
	font-family: var(--font-family);
}

a {
	color: unset;
	text-decoration: none;
}

a:hover,
button:hover {
	cursor: pointer;
}

.section-type {
	margin: 2rem 6rem;
	height: 75vh;
}

.title-heading {
	color: var(--color-heading);
}

.gradient-bg {
	/* ff 3.6+ */

	/* safari 5.1+,chrome 10+ */

	/* opera 11.10+ */
	background: -o-linear-gradient(90deg, #f8fcff 0%, #f3faff 100%);

	/* ie 6-9 */
	filter: progid:DXImageTransform.Microsoft.gradient(
			startColorstr='#F3FAFF',
			endColorstr='#F8FCFF',
			GradientType=0
		);

	/* ie 10+ */

	/* global 94%+ browsers support */
	background: linear-gradient(90deg, #f8fcff 0%, #f3faff 100%);
}

.gradient-text {
	background: var(--gradient-text);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

